.header {
  position: relative;
  background-color: #222;
  height: 60px;
  color: white;
}

.appTitle {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-left: 20px;
  font-size: 30px;
  font-weight: bold;
}



